<template>
    <h1>Send an Email</h1>
    <input type="email" placeholder="To:" v-model="email" required/>
    <br />
    <br />
    <br />
    <input type="text" placeholder="Subject:" v-model="subject"/>
    <br />
    <br />
    <br />
    <input type="text" placeholder="Body:" v-model="body"/>
    <br />
    <br />
    <br />
    <br />
    <button v-on:click="getMail">Confirm</button>
</template>

<script>
// import axios from "@/api/axios";
var validator = require("email-validator");
const qs = require('qs');

export default {
    name: "MailREA",
    data() {
        return {
            email: null,
            subject: null,  
            body: null,
        }
    },
    methods: {
        getMail(e) {
            e.preventDefault();
            if (this.email != null) {
                if (validator.validate(this.email)) {
                    let url = 'http://localhost:8080/ar-user/'
                    let mail = {
                        "to" : this.email,
                        "subject" : this.subject,
                        "text" : this.body
                    }
                    let res = {
                        "action": {
                            "id" : localStorage.actionId,
                            "content" : localStorage.action
                        },
                        "reaction": [ 
                            {
                                "id" : "6343d4cd453ec1ce28ef2b65",
                                "content" : JSON.stringify(mail)
                            }
                        ]
                    }
                    console.log(res)
                    fetch(url ,{
                        method: 'POST',
                        headers: {
                            'Content-Type': 'application/x-www-form-urlencoded',
                            'token' : localStorage.token,
                            "Accept" : "text/html",
                        },
                        body : qs.stringify(res),
                        mode : 'cors'
                    }
                    )
                    .then((res) => {
                        if (res.status === 201) {
                        res.text().then((ans) => {
                            console.log(ans)
                            this.$router.push({ name: "Home" });
                        })
                        } else {
                            console.log(res)
                            alert("Something Went Wrong");
                        }
                    })
                    .catch((err) => {
                        console.log(err);
                    });
                } else {
                    alert("Email is not valid")
                }
            } else {
                alert('Email required')
            }
        },
    }
}
</script>
